@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@300&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Varela+Round&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Ubuntu:wght@300&display=swap');

*{
  margin:0;
  padding:0;
  box-sizing: border-box;
}

.Body {
  background-color: #282c34;
  min-height: 100vh;
  font-family: 'Ubuntu',sans-serif;
}

.port::first-letter,.dot{
font-size: 30px;
}

.dot,.name,.about-span,.btn{
 color:tomato;/* color:rgb(136, 255, 0);*/
}

.skills-head{
  color:white;
}

.hor-line{
  border-color:tomato;/* rgb(136, 255, 0);*/
  border-width: 5px;
  width: 7em;
  animation: expand ease-out 3s;
}
.name{
  font-size: 4em;
  font-family: 'Varela Round';
}
.Intro{
  margin-top:7em;
}

@keyframes expand{
  0%{width: 0em;}
  100%{width:7em;}
}

.about-head,.projects-head{
  text-align:center;
  padding-top: 7em;
  color: #282c34;
}
.projects-head,.contact-head{
  padding-top: 4em;
}
.contact-head{
  text-align:center;
  color: white; 
}
.about-line,.projects-line,.contact-line{
  border-color: tomato;
  width:4em;
  border-width: 2px;
  margin:auto;
}
.pic{
  padding-top: 2em;
}
.about-intro{
  padding-top:5em;
}

.About-content,.projects-content{
  min-height: 95vh;
  background-color:white;
  margin-top: 10.5em;
}
.skills{
  margin-top:3em;
}

h3{
  color:white;
}

.con{
  background-color: tomato;
  color:white;
  border-radius: 5px;
}

.con:hover{
  background-color: rgb(253, 88, 59);
}

.navlink:hover{
background-color:tomato;
 /*rgba(255, 99, 71, 0.8);*/
/*  background-color:#cbd2df42;*/
  border-radius: 5px;
}

.contact-content{
height:fit-content;
}

.contact-content .btn{
  border:2px solid tomato;
  color:tomato;
  display:flex;
  justify-content:center;
  padding:20px 0px;
}

.contact-content .btn:hover{
  background-color:tomato;
  color:white;
}

.copyright{
  color:white;
}

.cs-row{
  padding:2em;
}

.copyright small{
  margin:auto;
  opacity: 0.6;
}

.card{
  margin: 30px;
}

.projects-row{
  display: flex;
  justify-content: space-around;
  align-items: center;
}

.hi{
  animation: launch 3s infinite;
}

.name{
  animation: name 3s ease;
}

h5{
  animation: heading 3s ease;
}

@keyframes launch{

   0%{transform:translateY(0em);}
   50%{transform:translateY(10em);}
   100%{transform:translateY(0em);}

}

@keyframes name{
  0%{transform: translateX(2em);
  opacity: 0.3;}
  100%{transform: translateX(0em);
    opacity:1;
  }
}

@keyframes heading{
  0%{transform: translateX(-3em);
  opacity: 0.3;}
  100%{transform: translateX(0em);
    opacity:1;
  }
}

.links{
  padding: 10px;
}

h2{
  margin:auto;
}

.card1{
  margin:10px;
}